.primary {
  background-color: #20014e;
}

.highlight {
  background-color: #ecab0a;
}

.section {
  width: 100vw;

  &.main-hero{
    height: 100vh;

    .hero-main-image{
      display: flex;
      align-items: center;
      justify-content: flex-end;

      & img {
        height: 100%;
      }
    }

    .hero-text-container {
      color: #FFF;
      text-shadow: #41a4ef 0 0 20px;

      .hero-title {
        font-size: 4.5rem;
      }

      .hero-subtitle {
        font-size: 2rem;
      }

    }
  }

  &.development-xp {
    height: 50vh;

    .xp-topic-container {
      height: 50%;
      list-style: none;
      padding: 10px;
      text-align: center;

      .topic-title {
        font-size: 40px;
        margin: 20px 0;
      }

      & li {
        list-style: none;
        margin: 10px 0;
        font-size: 26px;
      }

    }



  }
}
