.navbar {
  margin: 0;
  box-sizing: border-box;
  top: 0;
  width: 100vw;
  height: 80px;
  background: #34116d;
  color: #FFF;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  flex-direction: row;

    .container {
      width: 80%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-direction: inherit;
    }

    .nav-start, .nav-end {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: row;
      gap: 20px;
    }

    a {
      font-family: Montserrat, cursive;
      font-weight: 400;
      color: #FFF;
      opacity: .8;
      font-size: 1rem;
      text-decoration: none;
      padding: 10px;
      display: flex;
      align-items: center;
      justify-content: center;
      text-transform: uppercase;

      &:hover{
        opacity: 1;
      }

      &.mail-link {
        color: #ecab0a;
      }

      &.twitter-link {
        color: #1DA1F2;
      }
    }


    .nav-brand {
      font-size: 1.3em;
    }
}

