.columns{
  display: flex;
  height: inherit;
  align-items: center;
  justify-content: flex-start;
}

.column {
  width: inherit;

  &.is-half {
    width: 50%;
  }

  &.is-one-third {
    width: 33.33%;
  }

}

.container-80 {
  width: 80%;
  height: inherit;
  margin: 0 auto
}

.container-60 {
  width: 60%;
  height: inherit;
  margin: 0 auto
}

.flex {
  display: flex;
}

.h-centered {
  align-items: center;
}

.v-centered {
  justify-content: center;
}

.flex-centered {
  display: flex;
  align-items: center;
  justify-content: center;
}

.text-centered {
  text-align: center;
}

.adapt {
  height: 100%;
}

.fixed{
  position: fixed;
  top: 0;
  left: 0;
}
